
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 3em 0 0;
  font-family: "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  color: #666;
  text-align: center;
}
a {
  color: inherit;
  text-decoration: none;
}
h1 {
  margin-bottom: 1em;
  font-family: Dosis, "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
}
h1,
h2 {
  color: #2c3e50;
  font-weight: 300;
}
h2 {
  margin-top: 3em;
  font-size: 1.2em;
}
.desc {
  margin-bottom: 4em;
  color: #7f8c8d;
}
h2 small {
  opacity: 0.7;
}
p small {
  font-size: 0.8em;
  color: #7f8c8d;
}
p {
  line-height: 1.5;
}
pre {
  display: inline-block;
  padding: 0.8em;
  background-color: #f9f9f9;
  box-shadow: 0 1px 2px rgba(0,0,0,0.125);
  line-height: 1.1;
  color: #2973b7;
}
pre,
code {
  font-family: "Roboto Mono", Monaco, courier, monospace;
}
pre code {
  font-size: 0.8em;
}
.attr {
  color: #e96900;
}
.val {
  color: #42b983;
}
footer {
  margin: 5em 0 3em;
  font-size: 0.5em;
  vertical-align: middle;
}
footer a {
  display: inline-block;
  margin: 0 5px;
  padding: 3px 0 6px;
  color: #7f8c8d;
  font-size: 2em;
  text-decoration: none;
}
footer a:hover {
  padding-bottom: 3px;
  border-bottom: 3px solid #42b983;
}
button {
  border: 1px solid #4fc08d;
  border-radius: 2em;
  background-color: #fff;
  color: #42b983;
  cursor: pointer;
  font: inherit;
  transition: opacity 0.3s;
}
button:focus {
  outline: none;
  box-shadow: 0 0 1px #4fc08d;
}
button:active {
  background: rgba(79,192,141,0.2);
}
button[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}
button,
label {
  font-size: 0.75em;
}
figure {
  display: inline-block;
  margin: 2em auto;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 8px;
  box-shadow: 0 0 45px rgba(0,0,0,0.2);
  padding: 1.5em 2em;
}
figure .echarts {
  width: 40vw;
  min-width: 400px;
  height: 300px;
}
#logo {
  display: inline-block;
  width: 128px;
  height: 128px;
  pointer-events: none;
}
.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0,0,0,0.2);
}
.modal.open {
  display: block;
}
.modal img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 80vw;
  border: 2px solid #fff;
  border-radius: 3px;
  box-shadow: 0 0 30px rgba(0,0,0,0.2);
}
@media (min-width: 980px) {
figure.half {
    padding: 1em 1.5em;
}
figure.half .echarts {
    width: 28vw;
    min-width: 240px;
    height: 180px;
}
figure.half:not(:last-child) {
    margin-right: 15px;
}
}
@media (max-width: 980px) {
p {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
p select {
    text-indent: calc(50% - 1em);
}
p select,
  p label {
    border: 1px solid #4fc08d;
    border-radius: 2em;
    background-color: #fff;
    color: #42b983;
    cursor: pointer;
    transition: opacity 0.3s;
}
p button,
  p input,
  p select,
  p label {
    -webkit-box-flex: 1;
        -ms-flex: 1 0;
            flex: 1 0;
    margin: 0 0.5em;
    padding: 0;
    line-height: 2.4em;
    max-width: 40vw;
    border-radius: 2px;
    font-size: 0.8em;
}
p select {
    -webkit-appearance: none;
}
p input[type="checkbox"] {
    display: none;
}
p input[type="checkbox"]:checked + label {
    background: #42b983;
    color: #fff;
}
figure {
    width: 100vw;
    margin: 1em auto;
    padding: 0 1em;
    border: none;
    border-radius: 0;
    box-shadow: none;
}
figure .echarts {
    width: 100%;
    min-width: 0;
    height: 75vw;
}
}


